header{
  min-height: 70px;
  width:hidden;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid #ddd;
  background-color: transparent;
}
.Menu{
  color: black;
}

header.logo{
  flex: 1;
}

header a{
  text-transform: uppercase;
  color: black;
}
.profile{
  background-color:  #92a8d1;
  color: white;
  text-align: center;
  border-radius: 50%;
  height: 40px;
  width:50px;
}
header ul li{
  display: inline-block;
  opacity: 0.7;
  padding: 0 20px;
}
header ul li:hover{
  opacity: 1;
}
.menu{
  display: none;
}
.cart-icon{
  position: relative;
  background-color: none;
}
.cart-icon span{
  background: crimson;
 
  border-radius: 20px;
  color: white;
  position: absolute;
  top:-10px;
  right: -10px;
  padding: 5px 7px;
  font-size: 10px;
}